export function phone(e: React.KeyboardEvent<HTMLInputElement>) {
    // console.log(e)
    e.currentTarget.maxLength = 15;
    // console.log(e.currentTarget)
    // console.log(e.currentTarget.maxLength)
    let {value} = e.currentTarget;
    // console.log({value})
    if (e.key === 'Backspace') {
      return e;
    }
    value = value.replace(/\D/g, '');
    value = value.replace(/^(\d{2})/, '($1) ');
    value = value.replace(/(\d{5})/, '$1-');
    e.currentTarget.value = value;
    return e
  }

  export function cep(e: React.KeyboardEvent<HTMLInputElement>) {
    e.currentTarget.maxLength = 9;
  
    let { value } = e.currentTarget;
  
    if (e.key === 'Backspace') {
      return e;
    }
  
    value = value.replace(/\D/g, '');
    value = value.replace(/^(\d{5})/, '$1-');
  
    e.currentTarget.value = value;
  
    return e;
  }

  export function date(e: React.KeyboardEvent<HTMLInputElement>) {
    e.currentTarget.maxLength = 10;
  
    let { value } = e.currentTarget;
  
    if (e.key === 'Backspace') {
      return e;
    }
  
    value = value.replace(/\D/g, '');
    value = value.replace(/(\d{2})(\d)/, '$1/$2');
    value = value.replace(/(\d{2})(\d)/, '$1/$2');
  
    e.currentTarget.value = value;
  
    return e;
  }

  export function cpf(e: React.KeyboardEvent<HTMLInputElement>) {
    e.currentTarget.maxLength = 14;
  
    let { value } = e.currentTarget;
  
    if (value.length === 14 && !value.includes('.')) {
      const rawValue = value.slice(0, 11);
  
      const fixedValue = rawValue.replace(
        /(\d{3})(\d{3})(\d{3})(\d{2})/,
        '$1.$2.$3-$4',
      );
  
      e.currentTarget.value = fixedValue;
  
      return e;
    }
  
    if (e.key === 'Backspace') {
      return e;
    }
  
    value = value.replace(/\D/g, '');
    value = value.replace(/(\d{3})(\d)/, '$1.$2');
    value = value.replace(/(\d{3})(\d)/, '$1.$2');
    value = value.replace(/(\d{3})(\d{1,2})$/, '$1-$2');
  
    e.currentTarget.value = value;
  
    return e;
  }

  export function rg(e: React.KeyboardEvent<HTMLInputElement>) {
    e.currentTarget.maxLength = 10;
  
    let { value } = e.currentTarget;
  
    if (e.key === 'Backspace') {
      return e;
    }
  
    value = value.replace(/\D/g, '');
    value = value.replace(/^(\d{2})(\d{3})/, '$1.$2.');
    // value = value.replace(/(\d{3})(\d{3})/, '$1.$2-');
  
    e.currentTarget.value = value;
  
    return e;
  }