import * as React from "react";
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import * as Yup from "yup";
import { yupResolver } from "@hookform/resolvers/yup";
import { useForm } from "react-hook-form";
import {phone, cep, date, cpf, rg} from "./masks/masks"
import { useCallback } from "react";
import { Input } from "@mui/material"
import axios from "axios";


export default function Form() {

  const handleKeyUpPhone = useCallback(
      (e: React.KeyboardEvent<HTMLInputElement>) => {
        phone(e);
      },[]
    );

  const handleKeyUpCep = useCallback(
    (e: React.KeyboardEvent<HTMLInputElement>) => {
      cep(e);
    },[]
  );

  const handleKeyUpDate = useCallback(
    (e: React.KeyboardEvent<HTMLInputElement>) => {
      date(e);
    },[]
  );

  const handleKeyUpCpf = useCallback(
    (e: React.KeyboardEvent<HTMLInputElement>) => {
      cpf(e);
    },[]
  );

  const handleKeyUpRg = useCallback(
    (e: React.KeyboardEvent<HTMLInputElement>) => {
      cpf(e);
    },[]
  );

  const theme = createTheme();

  const api = axios.create({ baseURL: 'http://localhost:8080/intech-service/v1' });

  const validationSchemaObject = Yup.object({
    name: Yup.string().required("Campo obrigatório!"),
    email: Yup.string()
      .email("O e-mail inserido não é válido")
      .required("Campo obrigatório!"),
    password: Yup.string().required("Campo obrigatório!"),
    cpf: Yup.number().required("Campo obrigatório!"),
    rg: Yup.number().required("Campo obrigatório!"),
    residencial: Yup.number(),
    celular: Yup.number().required("Campo obrigatório!"),
    comercial: Yup.number(),
    nascimento: Yup.number().required("Campo obrigatório!"),
    endereço: Yup.string().required("Campo obrigatório!"),
    número: Yup.number().required("Campo obrigatório!"),
    bairro: Yup.string().required("Campo obrigatório!"),
    complemento: Yup.string(),
    cep: Yup.number().required("Campo obrigatório!"),
    cidade: Yup.string().required("Campo obrigatório!"),
    estado: Yup.string().required("Campo obrigatório!"),
  });

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({
    resolver: yupResolver(validationSchemaObject),
  });

  const handleSubmitData = async (e: any) => {
    const data = e;
    console.log(data)
    
    try {
      const response = await api.post("/customer", data)
      .then(() => {
        console.log("teste");
      }).catch((error) => {
        console.log("teste");
        console.log(error)
      });
    } catch (error) {
      console.log(error)
    }
  };

  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            marginTop: 8,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: "secondary.main" }}>

          </Avatar>
          <Typography component="h1" variant="h5">
            Formulário do Academy
          </Typography>
          <Box
            component="form"
            onSubmit={ handleSubmit(handleSubmitData)}
            sx={{ mt: 3 }}
          >
            <Grid container spacing={2}>
              <Grid item xs={12} sm={12} style={{ height: "79px" }}>
                <Input
                  fullWidth
                  id="name"
                  placeholder="Nome Completo*"
                  // autoFocus
                  type="text"
                  {...register("name")}
                />
                <span style={{ color: "red", fontSize: "12px" }}>
                  {errors?.name?.message}
                </span>
              </Grid>

              <Grid item xs={6} sm={6} style={{ height: "79px" }}>
                <Input
                  fullWidth
                  id="email"
                  placeholder="Seu e-mail*"
                  // autoFocus
                  {...register("email")}
                  type="email"
                />
                <span style={{ color: "red", fontSize: "12px" }}>
                  {errors?.email?.message}
                </span>
              </Grid>

              <Grid item xs={6} sm={6} style={{ height: "79px" }}>
                <Input
                  fullWidth
                  id="password"
                  placeholder="Senha*"
                  // autoFocus
                  type="password"
                  {...register("password")}
                />
                <span style={{ color: "red", fontSize: "12px" }}>
                  {errors?.password?.message}
                </span>
              </Grid>

              <Grid item xs={12} sm={6} style={{ height: "79px" }}>
                <Input
                  fullWidth
                  id="cpf"
                  placeholder="CPF*"
                  // autoFocus
                  type="text"
                  onKeyUp={handleKeyUpCpf}
                  {...register("cpf")}
                />
                <span style={{ color: "red", fontSize: "12px" }}>
                  {errors?.cpf?.message}
                </span>
              </Grid>

              <Grid item xs={6} sm={6} style={{ height: "79px" }}>
                <Input
                  fullWidth
                  id="rg"
                  placeholder="RG*"
                  // autoFocus
                  type="text"
                  onKeyUp={handleKeyUpRg}
                  {...register("rg")}
                />
                <span style={{ color: "red", fontSize: "12px" }}>
                  {errors?.rg?.message}
                </span>
              </Grid>

              <Grid item xs={6} sm={6} style={{ height: "79px" }}>
                <Input
                  fullWidth
                  id="residencial"
                  placeholder="Telefone Residencial*"
                  // autoFocus
                  type="text"
                  onKeyUp={handleKeyUpPhone}
                  {...register("residencial")}
                />
                <span style={{ color: "red", fontSize: "12px" }}>
                  {errors?.residencial?.message}
                </span>
              </Grid>

              <Grid item xs={6} sm={6} style={{ height: "79px" }}>
                <Input
                  fullWidth
                  id="celular"
                  placeholder="Celular*"
                  // autoFocus
                  type="text"
                  onKeyUp={handleKeyUpPhone}
                  {...register("celular")}
                />
                <span style={{ color: "red", fontSize: "12px" }}>
                  {errors?.celular?.message}
                </span>
              </Grid>

              <Grid item xs={6} sm={6} style={{ height: "79px" }}>
                <Input
                  fullWidth
                  id="comercial"
                  placeholder="Telefone Comercial"
                  // autoFocus
                  type="text"
                  onKeyUp={handleKeyUpPhone}
                  {...register("comercial")}
                />
                <span style={{ color: "red", fontSize: "12px" }}>
                  {errors?.comercial?.message}
                </span>
              </Grid>

              <Grid item xs={12} sm={6} style={{ height: "79px" }}>
                <Input
                  fullWidth
                  id="nascimento"
                  placeholder="Data de Nascimento"
                  // autoFocus
                  type="text"
                  onKeyUp={handleKeyUpDate}
                  {...register("nascimento")}
                />
                <span style={{ color: "red", fontSize: "12px" }}>
                  {errors?.nascimento?.message}
                </span>
              </Grid>

              <Grid item xs={12} sm={12} style={{ height: "79px" }}>
                <Input
                  fullWidth
                  id="endereço"
                  placeholder="Endereço"
                  // autoFocus
                  type="text"
                  {...register("endereço")}
                />
                <span style={{ color: "red", fontSize: "12px" }}>
                  {errors?.endereço?.message}
                </span>
              </Grid>

              <Grid item xs={5} sm={5} style={{ height: "79px" }}>
                <Input
                  fullWidth
                  id="número"
                  placeholder="Número"
                  // autoFocus
                  type="text"
                  {...register("número")}
                />
                <span style={{ color: "red", fontSize: "12px" }}>
                  {errors?.número?.message}
                </span>
              </Grid>

              <Grid item xs={7} sm={7} style={{ height: "79px" }}>
                <Input
                  fullWidth
                  id="bairro "
                  placeholder="Bairro"
                  // autoFocus
                  type="text"
                  {...register("bairro")}
                />
                <span style={{ color: "red", fontSize: "12px" }}>
                  {errors?.bairro?.message}
                </span>
              </Grid>

              <Grid item xs={12} sm={7} style={{ height: "79px" }}>
                <Input
                  fullWidth
                  id="complemento"
                  placeholder="Complemento"
                  // autoFocus
                  type="text"
                  {...register("complemento")}
                />
                <span style={{ color: "red", fontSize: "12px" }}>
                  {errors?.complemento?.message}
                </span>
              </Grid>

              <Grid item xs={6} sm={5} style={{ height: "79px" }}>
                <Input
                  fullWidth
                  id="cep"
                  placeholder="Cep"
                  // autoFocus
                  type="text"
                  onKeyUp={handleKeyUpCep}
                  {...register("cep")}
                />
                <span style={{ color: "red", fontSize: "12px" }}>
                  {errors?.cep?.message}
                </span>
              </Grid>

              <Grid item xs={6} sm={6} style={{ height: "79px" }}>
                <Input
                  fullWidth
                  id="cidade"
                  placeholder="Cidade"
                  // autoFocus
                  type="text"
                  {...register("cidade")}
                />
                <span style={{ color: "red", fontSize: "12px" }}>
                  {errors?.cidade?.message}
                </span>
              </Grid>

              <Grid item xs={12} sm={6} style={{ height: "79px" }}>
                <Input
                  fullWidth
                  id="estado"
                  placeholder="Estado"
                  // autoFocus
                  type="text"
                  {...register("estado")}
                />
                <span style={{ color: "red", fontSize: "12px" }}>
                  {errors?.estado?.message}
                </span>
              </Grid>

            </Grid>
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              Se cadastrar!
            </Button>
          </Box>
        </Box>
      </Container>
    </ThemeProvider>
  );
}
function setIn(formError: any, path: any, message: any) {
  throw new Error("Function not implemented.");
}

